<template>
  <div>
    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title>
          불이합성 MES SYSTEM
        </v-card-title>
        <v-card-text style="padding-bottom:0px;">
        	<v-text-field label="ID" icon="person" v-model="username"/>
    		<v-text-field label="Password" icon="lock" type="password" v-model="password" v-on:keyup.enter="LoginConfirm" />
		</v-card-text>
        <v-card-actions style="padding-top:0px;">
          <v-spacer></v-spacer>
          <v-btn color="black darken-1" text v-on:click="LoginConfirm">
            로그인
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script src="https://unpkg.com/vue"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/Chart.js/2.4.0/Chart.min.js"></script>
<script>
import axios from 'axios';

export default {
	data () {
      return {
		  username: '',
		  password: '',
		  dialog: false
      }
    },
	components: {
	},
    mounted() {
	    if (!this.$session.exists()) {
			this.dialog = true
		}
	},
	methods: {
		LoginConfirm() {
			if(this.username == '' || this.password == ''){
				alert('ID와 비밀번호를 입력해주시기 바랍니다.');
				return;
			}
		
			axios.post('/back/Login/Login', null, { params: {
				clm_user_id: this.username,
				clm_user_pw: this.password
			}})
			.then(response => {
				if (response.status == '200' && response.data.clm_user_id != '') {
					this.$session.start();
					this.$session.set('user_id', response.data.clm_user_id);
					this.$session.set('user_name', response.data.clm_user_name);
					this.$session.set('user_machine_yn', response.data.clm_machine_yn);
					this.$session.set('user_machine_type', response.data.clm_machine_type);
					// alert('정상적으로 로그인 되었습니다.');
					if(this.$isMobile()){
						axios.get('/back/Common/CellPhoneList',{
							params: {
								authority_id: this.$session.get('user_authority')
								}
							})
							.then(response => {
								var id =response.data[0].clm_screen_id;
								this.$router.push(id);
							});
					}else{
						location.reload();
					}
				}else{
					alert('ID와 패스워드를 확인해주시기 바랍니다.');
					this.$session.destroy();
				}
			})
			.catch(error => {
				console.log(error)
			})
		}
	}
}
</script>